	module.exports = {"data":{"markdownRemark":{"html":"<p>仕事で嵐山～大宮間を何往復もしているが平均片道所要時間45分……俺が今までスピード違反で捕まってないのは相当の奇跡なんだろうかｗ まぁパトカーを抜かない・白バイは車2～3台挟んで後ろを走る、これだけで大抵は大丈夫ですが……ネズミ捕り以外はｗ  チャリに代わる代用の乗り物って割り切るならスズキが定価6万で出してるチョイノリだって代用になるぜ。ただ最高速40km/hでも良ければ、なｗ 全部が全部60km/hまで出せるとは限らない。国道や県道で箱トラや大型トレーラーとかにビュンビュン抜かれていくのはいろいろと恐怖を感じるぜ。俺でも以前乗ってたリード50は68km/h、今乗ってるYB-1は77km/hまで出るけどそれでも後ろの車にギリギリまで引っ付かれて煽られるからすっげー怖いもん。だから今400の免許取るべく奮戦中なのだｗ 流石に400は話逸れたが、そう考えると原付なら何でもいいってわけじゃないって思えてくるだろ？ とりあえず原付買う前に最低でも5万、出来れば8万用意して置いた方がいいな。本体は中古ならさほどしないが、自賠責・任意保険、あとメットとグローブは必須だぜ。軍手じゃ今の季節凍えてブレーキすら握れなくなるからな。あとヘルメットは半帽だと事故った時マジで死ぬから最低でもシールド付ジェット、出来ればフルフェイスだな。そう考えるとやっぱ8万近く行っちゃう。まぁ本体だけ先買って装備は後日買うってテもあるがな。</p>","frontmatter":{"cover":null,"date":"2004-11-02T00:00:00+09:00","strDate":"2004/11/1","title":"バイクの選び方 2(半分駄文)"}}},"pathContext":{"path":"/ja/blog/2004/11/02-バイクの選び方-2半分駄文/","langKey":"ja"}}

